/* App.css */
body {
  font-family: 'Segoe UI', sans-serif;
  background-color: #e0e0eb;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 600px;
  margin: 50px auto;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
  background-color: white;
  display: flex;
  flex-direction: column;
  height: 80vh;
}

h1 {
  text-align: center;
  margin: 0;
  padding: 16px;
  background: #3288ea;
  color: white;
  font-size: 1.5rem;
}

.chat-box {
  flex: 1;
  padding: 16px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 10px;
  background: #f0f2f5;
}

.message {
  max-width: 75%;
  padding: 10px 14px;
  border-radius: 18px;
  line-height: 1.4;
  font-size: 0.95rem;
}

.message.you {
  align-self: flex-end;
  background-color: #fcd46f;
  color: #69e1f9;
  border-bottom-right-radius: 0;
}

.message.bacchu {
  align-self: flex-start;
  background-color: #e7f1ff;
  color: #46c9f9;
  border-bottom-left-radius: 0;
}

.input-row {
  display: flex;
  padding: 12px;
  border-top: 1px solid #e36e6e;
  background: #62b7f9;
}

input[type="text"] {
  flex: 1;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 20px;
  outline: none;
  margin-right: 10px;
  font-size: 1rem;
}

button {
  background-color: #4c6ef5;
  color: white;
  padding: 10px 16px;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  font-size: 1rem;
  transition: background 0.2s;
}

button:hover {
  background-color: #3b5bdb;
}

.avatar-container {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
  animation: fadeIn 0.3s ease-in-out;
}

.avatar {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid #ccc;
}

.typing-text {
  color: #666;
  font-style: italic;
  font-size: 0.9rem;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(10px); }
  to { opacity: 1; transform: translateY(0); }
}

/* ðŸŒ™ Dark Mode Styles */
.container.dark {
  background: #1e1e1e;
  color: #f1f1f1;
}

.container.dark .chat-box {
  background: #2a2a2a;
}

.container.dark .message.you {
  background-color: #336d4f;
}

.container.dark .message.bacchu {
  background-color: #3c3c3c;
}

.container.dark .input-row input[type="text"] {
  background-color: #333;
  color: #f1f1f1;
  border: 1px solid #666;
}

.container.dark button {
  background-color: #444;
  color: white;
}

.toggle-btn {
  margin-top: 10px;
  padding: 6px 12px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  background: #ccc;
}
